.Home-Page img{width:100%;max-width:50vw;aspect-ratio:1/1;object-fit:cover;border-radius:50%;animation:rollIn 1s ease-out}.Home-Page .Text-Zone{width:100%;position:relative;text-align:left;display:flex;flex-direction:column;font-family:"Khula",sans-serif;gap:0px}.Home-Page .Text-Zone::before{content:"<main>";font-size:14px;top:-40px;left:-20px;color:gray;position:absolute}.Home-Page .Text-Zone::after{content:"<⁄main>";font-size:14px;left:-14px;bottom:-25px;color:gray;position:absolute}.Home-Page .Text-Zone h1{position:relative;font-family:"Khula",sans-serif;font-weight:400;font-size:3rem;letter-spacing:2px;line-height:3.2rem}.Home-Page .Text-Zone h1 span{color:#00adb5;font-weight:700}.Home-Page .Text-Zone h1::before{content:"<h1>";font-size:14px;top:-32px;color:gray;position:absolute;letter-spacing:normal}.Home-Page .Text-Zone h1::after{content:"<⁄ h1>";font-size:14px;bottom:-32px;color:gray;position:absolute;letter-spacing:normal}.Home-Page .Text-Zone p{margin-top:20px;font-family:"Khula",sans-serif;font-weight:400;font-size:1.75rem;letter-spacing:3px;margin-bottom:50px}.Home-Page .Text-Zone p span{color:#00adb5}.Home-Page .Text-Zone a{text-align:center}.Home-Page .Contact-Button{color:#00adb5;font-size:1.75rem;font-family:"Khula",sans-serif;font-weight:600;letter-spacing:4px;text-decoration:none;padding:20px 0px;border:1px solid #00adb5;white-space:nowrap;margin-right:20px}.Home-Page .Contact-Button:hover{background:#00adb5;color:#eee}@media(min-width: 700px){.Home-Page{flex-direction:row;padding:50px 0 50px 0}.Home-Page img{object-fit:contain;max-height:70vh;height:calc(100vh - 50px);border-radius:15px;animation:fadeIn 1s ease-out}}